import {
  ArrowTable,
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import * as d3 from "d3"
import legend from 'd3-svg-legend'
import "./BubblePlotComponent.scss"

var D3_DIV_ID: string;

type dataObj = {
  index: number,
  episode: number,
  act: string,
  chapter: string,
  segment: string,
  start_time: number,
  end_time: number,
  viewership: number
}

/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class BubblePlotComponent extends StreamlitComponentBase {
  private get_data(): dataObj[] {
    const table: ArrowTable = this.props.args["data"];

    var data : dataObj[] = [];
    const num_columns = table.columns;
    // Itterate from 1 because of headerRow
    for (let rowIndex = 1; rowIndex < table.rows; rowIndex++) {
      const row = d3.range(0, num_columns).map((columnIndex) => {
        const { classNames, content, id, type } = table.getCell(rowIndex, columnIndex);
        return content?.toString()
      });

      data.push({index:rowIndex,
        episode:parseInt(row[1]),
        act:row[2],
        chapter:row[3],
        segment:row[4],
        start_time:parseFloat(row[5]),
        end_time:parseFloat(row[6]),
        viewership:parseFloat(row[7])});
    }

    return data;
  }

  private get_metadata() {
    return this.props.args.metadata
  }

  private get_theme() {
    return this.props.theme
  }

  private get_dimensions(): [number, number, {top:number, right:number, bottom:number, left:number}] {
    // set the dimensions and margins of the graph
    const margin = {top: 80, right: 150, bottom: 80, left: 80},
          width = 1200 - margin.left - margin.right,
          height = 600 - margin.top - margin.bottom;

    return [width, height, margin]
  }



  public componentDidMount = (): void => {
    const [width, height, margin] = this.get_dimensions();

    function resizeToFit(svg) {
      // get container + svg aspect ratio
      var container = d3.select(svg.node().parentNode),
          width = parseInt(svg.style("width")),
          height = parseInt(svg.style("height")),
          aspect = width / height;

      // add viewBox and preserveAspectRatio properties,
      // and call resize so that svg resizes on inital page load
      svg.attr("viewBox", "0 0 " + width + " " + height)
          .attr("perserveAspectRatio", "xMinYMid")
          .call(resize);

      d3.select(window).on("resize." + container.attr("id"), resize);

      // get width of container and resize svg to fit it
      function resize() {
          var targetWidth = parseInt(container.style("width"));
          svg.attr("width", targetWidth);
          svg.attr("height", Math.round(targetWidth / aspect));
          Streamlit.setFrameHeight();
      }
    }


    const svg = d3.select("#" + D3_DIV_ID)
                  .append("svg")
                    .attr("width", width + margin.left + margin.right)
                    .attr("height", height + margin.top + margin.bottom)
                    .call(resizeToFit)
                  .append("g")
                    .attr("transform", `translate(${margin.left}, ${margin.top})`);

    const clip = svg.append("defs").append("svg:clipPath")
                    .attr("id", "clip")
                    .append("svg:rect")
                    .attr("width", width )
                    .attr("height", height )
                    .attr("x", 0)
                    .attr("y", 0);

    svg.append("rect")
        .attr("id", "zoomRect")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height)
        .style("fill", "none")
        .style("pointer-events", "all")
        .attr('transform', 'translate(' + -margin.left + ',' + 0 + ')');

    // Add axes
    svg.append("g")
        .attr("transform", `translate(0,${height})`)
        .attr("id", "myXaxis");
    svg.append("g")
        .attr("id", "myYaxis");

    // Add tooltip
    d3.select("body")
        .append("div")
        .attr("class", "svg-tooltip")
        .style("font-family", `-apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple   Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"`)
        .style("border-radius", ".1rem")
        .style("font-size", "14px")
        .style("max-width", "320px")
        .style("text-overflow", "ellipsis")
        .style("white-space", "pre")
        .style("z-index", "300")
        .style("background", "rgba(69,77,93,.9)")
        .style("color", "#fff")
        .style("display", "block")
        .style("padding", ".2rem .4rem")
        .style("position", "absolute")
        .style("visibility", "hidden");

    svg.append("text")
        .attr("id", "yLabel")
        .attr("transform", "rotate(-90)")
        .attr("y", -margin.left)
        .attr("x", -(height / 2))
        .attr("dy", "1em")
        .style("font-size", 20)
        .style("text-anchor", "middle");

    svg.append("g")
        .attr("class", "legendSize")
        .attr("transform", `translate(${width}, 20)`);

    Streamlit.setFrameHeight();
  }



  public componentDidUpdate(): void {
    const data = this.get_data(),
          metadata = this.get_metadata(),
          [width, height, margin] = this.get_dimensions(),
          colour = metadata.colour,
          svg = d3.select("#" + D3_DIV_ID).select("svg").select("g");


    var chapterXaxis = {};

    metadata.chapter_order.map((d, i) => {
      if (!(d in chapterXaxis)) {
        chapterXaxis[d] = i+1;
      }
    });

    const NUM_CHAPTERS = Object.keys(chapterXaxis).length;

    var time_sorted_data = data.sort((a, b) => {
      if (a.end_time - a.start_time > b.end_time - b.start_time) {
        return -1
      }
      else if (a.end_time - a.start_time < b.end_time - b.start_time) {
        return 1
      }
      return 0
    });

    const x = d3.scaleLinear()
                .domain([0, NUM_CHAPTERS+1])
                .range([0, width]),
          y = d3.scaleLinear()
                .domain([0, d3.max(data, d => d.viewership) * 1.1 ])
                .range([height, 0]);

    // The radius scales so that the area of the bubble represents the segment time.
    // It is scaled so that in episode view the average bubble will take up half of its episodes space.
    const radius = d3.scaleLinear()
                    .domain([0, Math.sqrt(d3.mean(data, d => d.end_time - d.start_time)) / Math.PI])
                    .range([0, Math.min(30, (width / d3.max(data, d => d.episode) / 2 * 0.5))]);

    const xAxis = d3.select("#myXaxis"),
          yAxis = d3.select("#myYaxis");

    var tick_strings = {};
    Object.keys(chapterXaxis).map((d, i) => {
      tick_strings[i+1] = d;
    });

    xAxis.call(d3.axisBottom(x)
              .ticks(NUM_CHAPTERS)
              .tickFormat(d =>tick_strings[d]));

    yAxis.call(d3.axisLeft(y)
                  .tickSize(-width));

    yAxis.selectAll("text")
          .style("font-size", "15");

    const tooltip = d3.select("body").select(".svg-tooltip");

    var doSwapView = false;
    if (!svg.select("#dataCircle").empty()) {
      if (svg.select("#dataCircle").attr("view") === "episode") {
        doSwapView = true;
      }
    }

    const distinct = (value, index, self) => {
      return self.indexOf(value) === index;
    }

    const acts = data.map(d => d.act).filter(distinct);
    var act_starts = Object.fromEntries(acts.map(act => [act, d3.max(data.map(d => d.episode))])),
        act_stops = Object.fromEntries(acts.map(act => [act, 0]));
    data.map(function(d) {
      if (d.episode < act_starts[d.act]) {
        act_starts[d.act] = d.episode
      }
      if (d.episode > act_stops[d.act]) {
        act_stops[d.act] = d.episode
      }
    });

    // const act_line_data = Object.entries(act_stops).map(d => ({act:d[0], stop:d[1] + 0.5, start:0}));
    const act_line_data: Object[] = [];
    for (let i = 0; i < Object.entries(act_starts).map(d => d[0]).length; i++) {
      const act = Object.entries(act_starts).map(d => d[0])[i];
      act_line_data.push({act:act, start:act_starts[act] - 0.5, stop:act_stops[act] + 0.5});
    }

    svg.selectAll(".actLine")
        .data(act_line_data)
        .join("path")
          .attr("clip-path", "url(#clip)")
          .attr("d", () => { var path = d3.path()
                             path.moveTo(0, 0)
                             path.lineTo(0, height)
                             return path;
                           })
          .attr("fill", "none")
          .attr("stroke", "black")
          .attr("stroke-width", 1)
          .style("stroke-dasharray", "4, 4")
          .attr("class", "actLine");

    svg.selectAll(".actRect")
        .data(act_line_data)
        .join("rect")
          .attr("clip-path", "url(#clip)")
          .style("pointer-events", "none")
          // .attr('transform', 'translate(' + -margin.left + ',' + 0 + ')')
          .attr("fill", d => metadata.act_colour[d.act])
          .style("opacity", "0")
          .attr("width", "0")
          .attr("height", height)
          .attr("class", "actRect");

    svg.selectAll(".actLabel")
        .data(act_line_data)
        .join("text")
          .text(d => d.act)
          .attr("transform", "translate(0, -5)")
          .style("opacity", "0")
          .style("text-anchor", "middle")
          .attr("class", "actLabel");

    svg.selectAll("#dataCircle")
        .data(time_sorted_data)
        .join("circle")
          .attr("id", "dataCircle")
          .attr("view", "chapter")
          .attr("clip-path", "url(#clip)")
          .attr("r", d => radius(Math.sqrt(d.end_time - d.start_time) / Math.PI))
          .attr("cx", d => x(chapterXaxis[d.chapter]))
          .attr("cy", d => y(d.viewership))
          .attr("fill", d => colour[d.chapter])
          .style("opacity", "0.7")
          .style("pointer-events", "auto")
          .on("mouseover", function(event, d) {
            d3.select(event.target)
              .attr("stroke-width", "1")
              .attr("stroke", "black");

            tooltip.style("visibility", "visible")
                    .text(`Viewership: ${Math.round(d.viewership)}${metadata.metric_postfix}\nEpisode: ${d.episode}\n` +
                          `Act: ${d.act}\nChapter: ${d.chapter}\nSegment: ${d.segment}\n` +
                          `Start(s): ${d.start_time}\nEnd(s): ${d.end_time}`);
          })
          .on("mousemove", function(event) {
            tooltip
              .style("top", event.pageY - 10 + "px")
              .style("left", event.pageX + 10 + "px");
          })
          .on("mouseout", function(event) {
            d3.select(event.target)
              .attr("stroke-width", null)
              .attr("stroke", null);

            tooltip.style("visibility", "hidden");
          })
          .attr("view", "chapter");

    svg.select("#yLabel")
        .text(`${metadata.metric}`);

    const MAX_TIME_POWER10 = Math.pow(10, Math.floor(Math.log10(d3.max(data, d => (d.end_time - d.start_time))))),
          legendDomain = [0.1, 0.25, 0.5, 1].map(d => d * MAX_TIME_POWER10),
          legendRange = legendDomain.map(d => radius((Math.sqrt(d) / Math.PI)))


    var legendRadiusScale = d3.scaleOrdinal()
                              .domain(legendDomain.map(d => `${d}s`))
                              .range(legendRange);

    var sizeLegend = legend.legendSize()
                            .ascending(true)
                            .title("Segment Time")
                            .shape("circle")
                              .shapePadding(5)
                              .labelOffset(10)
                              .orient("vertical")
                              .scale(legendRadiusScale);

    svg.select(".legendSize")
        .call(sizeLegend)
        .selectAll("circle")
          .style("opacity", 0.5);

    svg.select(".legendSize")
        .selectAll("text")
        .style("font-size", "20");


    const zoomChart = (event) => {
      if (isNaN(event.transform.k)) {
        return
      }
      var newY = event.transform.rescaleY(y);
      yAxis.call(d3.axisLeft(newY)
                    .tickSize(-width))

      yAxis.selectAll("text")
            .style("font-size", "15");

      svg.selectAll("#dataCircle")
          .attr('cy', function(d) {return newY(d.viewership)});
    }

    var zoom = d3.zoom()
                  .scaleExtent([1, 20])
                  .extent([[0, 0], [width, height]])
                  .on("zoom", zoomChart);

    svg.select("#zoomRect")
        .call(zoom);

    // This is to fix the zoom after updating the component with new data or option choices.
    document.getElementById("zoomRect")?.dispatchEvent(new WheelEvent("wheel", {deltaY: 1}));
    document.getElementById("zoomRect")?.dispatchEvent(new WheelEvent("wheel", {deltaY: -1}));

    if (doSwapView) {
      this.swapView(metadata.chapter_order, 0);
    }
    this.showAll(0);

    // Ripple effect on buttons: https://codepen.io/dazar-dev/pen/QWNyKMO
    function animateRippleButton(event) {
      const ANIMATION_SPEED: number = 800

        let x: number = event.clientX - event.target.offsetLeft
        let y: number = event.clientY - event.target.offsetTop

        const ripple: HTMLElement = document.createElement("div")

        ripple.style.left = x + "px"
        ripple.style.top = y + "px"
        event.target.append(ripple)

        setTimeout(() => {
          ripple.remove()
        }, ANIMATION_SPEED)
    }

    d3.select("#swap_view_button")
        .on("click", (event) => {this.swapView(metadata.chapter_order); animateRippleButton(event);});

    d3.select("#show_all_button")
      .on("click", (event) => {this.showAll(); animateRippleButton(event);});


    // !!!CHATGPT
    function getReadableTextColor(backgroundHex) {
      // Remove the '#' symbol if present
      backgroundHex = backgroundHex.replace('#', '');

      // Parse the background hex color components
      const bgRed = parseInt(backgroundHex.substr(0, 2), 16);
      const bgGreen = parseInt(backgroundHex.substr(2, 2), 16);
      const bgBlue = parseInt(backgroundHex.substr(4, 2), 16);

      // Calculate the relative luminance of the background color
      const bgLuminance = (0.299 * bgRed + 0.587 * bgGreen + 0.114 * bgBlue) / 255;

      // Calculate the target luminance for the text color
      const textLuminance = 0.1; // Adjust this value if desired

      // Calculate the contrast ratio between the text and background colors
      const contrastRatio = (Math.max(bgLuminance, textLuminance) + 0.05) / (Math.min(bgLuminance, textLuminance) + 0.05);

      // If the contrast ratio is sufficient, return black or white accordingly
      if (contrastRatio >= 4.5) {
        return '#000000'; // Black
      } else {
        return '#FFFFFF'; // White
      }
    }

    const chapter_button_container = d3.select("#chapter_button_container");
    const swapShowChapter = this.swapShowChapter
    chapter_button_container
      .selectAll(".ripple_button")
      .data(Object.keys(chapterXaxis))
      .join("a")
        .attr("id", d => `show_${d}_button`)
        .attr("class", "ripple_button")
        .style("background-color", d => colour[d])
        .style("color", d => getReadableTextColor(colour[d]))
        .text(d => d)
        .on("click", function(this: any, event, d) {
                      d3.select(this).style("opacity", d3.select(this).style("opacity") === "1" ? "0.5": "1");
                      swapShowChapter(d)
                    });

    Streamlit.setFrameHeight();
  }



  public render = (): ReactNode => {
    D3_DIV_ID = "bubble_component_" + this.props.args["key"];


    return (
      <span>
        <div id={D3_DIV_ID}></div>

        <div className="button_container" id="chapter_button_container"></div>
        <div className="button_container">
          <a id="show_all_button" className="ripple_button">Show All Chapters</a>
          <a id="swap_view_button" className="ripple_button">To Episode View</a>
        </div>

      </span>
    )
  }


  private swapView(chapter_order?, duration=800) {
    const svg = d3.select("#" + D3_DIV_ID).select("svg").select("g");

    if (svg.select("#dataCircle").attr("view") === "chapter") {
      d3.select("#swap_view_button").text("To Chapter View");
      toEpisodeView(duration);
      return;
    }
    if (svg.select("#dataCircle").attr("view") === "episode") {
      d3.select("#swap_view_button").text("To Episode View");
      toChapterView(chapter_order, duration);
      return;
    }


    function toEpisodeView(duration=800) {
      const margin = {top: 80, right: 150, bottom: 80, left: 80},
            width = 1200 - margin.left - margin.right,
            height = 600 - margin.top - margin.bottom;

      const svg = d3.select("#" + D3_DIV_ID).select("svg").select("g");

      var maxEpisode = 0;

      svg.selectAll("#dataCircle")
          .each(function(d) {
            if (d.episode > maxEpisode) {
              maxEpisode = d.episode;
            }
          });

      const episodeX = d3.scaleLinear()
                      .domain([0, maxEpisode+1])
                      .range([0, width])

      const xAxis = d3.select("#myXaxis");

      xAxis.transition().duration(duration)
            .call(d3.axisBottom(episodeX)
                    .ticks(maxEpisode)
                    .tickFormat(function(d) {if (d > 0 && d <= maxEpisode) { return `Episode ${d}` }}));

      svg.selectAll("#dataCircle")
          .attr("view", "episode")
          .transition().duration(duration)
            .attr("cx", d => episodeX(d.episode));

      svg.selectAll(".actLine")
          .transition().duration(duration)
          .attr("d", d => { console.log(d)
                            var path = d3.path()
                            path.moveTo(episodeX(d.stop), 0)
                            path.lineTo(episodeX(d.stop), height)
                            return path;
                          });

      svg.selectAll(".actLabel")
          .transition().duration(duration)
          .style("opacity", "1")
          .attr("transform", d => `translate(${episodeX((d.stop + d.start) / 2)}, -5)`);

      svg.selectAll(".actRect")
          .transition().duration(duration)
          .style("opacity", "0.2")
          .attr("width", d => episodeX(d.stop) - episodeX(d.start))
          .attr("transform", d => `translate(${episodeX(d.start)}, 0)`);

    }


    function toChapterView(chapter_order?, duration=800) {
      const margin = {top: 80, right: 150, bottom: 80, left: 80},
            width = 1200 - margin.left - margin.right,
            height = 600 - margin.top - margin.bottom;

      const svg = d3.select("#" + D3_DIV_ID).select("svg").select("g");

      var chapterXaxis = {};

      chapter_order?.map((d, i) => {
        if (!(d in chapterXaxis)) {
          chapterXaxis[d] = i+1;
        }
      });

      const NUM_CHAPTERS = Object.keys(chapterXaxis).length;

      const chapterX = d3.scaleLinear()
                  .domain([0, NUM_CHAPTERS+1])
                  .range([0, width]);

      const xAxis = d3.select("#myXaxis");

      var tick_strings = {};
      Object.keys(chapterXaxis).map((d, i) => {
        tick_strings[i+1] = d;
      });

      xAxis.transition().duration(duration)
            .call(d3.axisBottom(chapterX)
                    .ticks(NUM_CHAPTERS)
                    .tickFormat(d =>tick_strings[d]));

      svg.selectAll("#dataCircle")
          .attr("view", "chapter")
          .transition().duration(duration)
            .attr("cx", d => chapterX(chapterXaxis[d.chapter]));

      svg.selectAll(".actLine")
          .transition().duration(duration)
          .attr("d", d => { var path = d3.path()
                            path.moveTo(0, 0)
                            path.lineTo(0, height)
                            return path;
                          });

      svg.selectAll(".actLabel")
          .transition().duration(duration)
          .style("opacity", "0")
          .attr("transform", "translate(0, -5)");

      svg.selectAll(".actRect")
          .transition().duration(duration)
          .style("opacity", "0")
          .attr("width", "0")
          .attr("transform", "translate(0, 0)");
    }
  }

  private showAll(duration=400) {
    const svg = d3.select("#" + D3_DIV_ID).select("svg").select("g");
    svg.selectAll("#dataCircle")
        .transition().duration(duration)
        .style("opacity", "0.7")
        .style("pointer-events", "auto");

    d3.select("#chapter_button_container")
      .selectAll(".ripple_button")
      .style("opacity", "1");
  }

  private swapShowChapter(chapter: string, duration=400) {
    const svg = d3.select("#" + D3_DIV_ID).select("svg").select("g");

    svg.selectAll("#dataCircle")
        .transition().duration(duration)
        .style("opacity", function(this: any, d) {
          if (d.chapter === chapter) {
            if (d3.select(this).style("opacity") === "0.7") {
              return "0";
            }
            return "0.7";
          }
          return d3.select(this).style("opacity");
        })
        .style("pointer-events", function(this: any, d) {
          if (d.chapter === chapter) {
            if (d3.select(this).style("pointer-events") === "auto") {
              return "none";
            }
            return "auto"
          }
          return d3.select(this).style("pointer-events");
        });
  }
}


export default withStreamlitConnection(BubblePlotComponent)
